.container {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	gap: 10px;
  height: 2340px;
	width: 95%;
  top: 0px;
	flex-wrap: wrap;
}
.obj3d {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-linejoin: round;
}
.ai {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-linejoin: round;
}
.digitalart {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-linejoin: round;
}
.programmer {
	position: sticky;
	top: 5%;
