}
.obj3d-container, .ai-container, .digitalart-container, .programmer-container {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: center;
	gap: 10px;
  height: 1900px;
  top: 0px;
	flex-wrap: wrap;
}
.obj3d-container-whole, .ai-container-whole, .digitalart-container-whole, .programmer-container-whole {
	position: relative;
	display: flex;
	flex-direction: row;
  justify-content: center;
  top: 0px;
	flex-wrap: wrap;
}

.rippler {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  bottom: 10%;
  height: 80px;
  width: 80px;
  border-radius: 100%;
  /* box-shadow: 0 0 20px 0 rgba(255, 0, 0, 0.5); */
  &::after {
    opacity: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -5px;
    left: -5px;
    right: 0;
    bottom: 0;
    content: '';
    height: 100%;
    width: 100%;
    /* border: 6px solid #ffd0d0; */
    background: #202020;
    border-radius: 100%;
    animation-name: ripple;
    animation-duration: 3s;
    animation-delay: 0.2s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(.65,0,.34,1);
    z-index: 1;
  }
}
@keyframes ripple {
  0% {
    opacity: 0.4;
    transform: scale3d(0,0,1);
  }
  

  100% {
    opacity: 0;
    transform: scale3d(2,2,1);
  }
}


