.property-onpress {
  position: absolute;
  width: 300px;
  height: 400px;
  display: flex;
  justify-content: center;
  bottom: 60px;
}

.overlayfake{
  bottom: 0px;
  position: absolute;
  opacity: 0;
  z-index: 1;
  transition: opacity 1s;
}
.overlay{
  position: absolute;
}
.underlay {
  height: 400px;
  width: 300px;
  background-color: var(--color-maroon);
  opacity: 0;
}
.underlayfake{
  position: absolute;
  width: 300px;
  height: 343px;
  flex-shrink: 0;
  bottom: 0%;
  fill: rgba(135, 0, 0, 1);
  box-shadow: 4px -4px 6px 0px #FF3535 inset, -4px 0px 6px 0px #FF3535 inset, 1px -1px 4px 0px #000 inset, -1px 1px 4px 0px #000 inset;
  filter: drop-shadow(-4px 4px 40px rgba(255, 53, 53, 0.15)) drop-shadow(4px 4px 40px rgba(255, 53, 53, 0.15)) blur(14px);
  z-index: -1;
  opacity: 0;
  transition: opacity 1s;
}
.property-onpress:hover .underlay{
  opacity: 1;
}
.property-onpress:hover .overlayfake, .property-onpress:hover .underlayfake{
  transition: 500ms;
  opacity: 1;
}
